import {Client} from "../../client/metier/client.metier";
export class Maintenance {
  id:number;
  numero:number = 0;
  client:Client = null;
  date:Date = null;
  typeContrat:string = null;
  typeFacturation:string = null;
  montant:number = 0;

  constructor(values:Object = {}) {
    Object.assign(this, values);
  }
}



