import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators, FormControl} from '@angular/forms';
import {PageTitleService} from '../../core/page-title/page-title.service';
import {fadeInAnimation} from "../../core/route-animation/route.animation";
import {Maintenance} from "../metier/maintenance.metier";
import {MaintenanceService} from "../service/maintenance.service";
import {BaseComponent} from "../../core/component/base-component";
import {Params, ActivatedRoute} from "@angular/router";
import {Client} from "../../client/metier/client.metier";
import {ClientService} from "../../client/service/client.service";
import {CustomValidators} from "ng2-validation/dist/index";

@Component({
  selector: 'ms-maintenance-edit',
  templateUrl: './maintenance.edit.component.html',
  styleUrls: ['./maintenance.edit.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class MaintenanceEditComponent extends BaseComponent implements OnInit {

  private maintenance: Maintenance;
  public maintenanceForm: FormGroup;
  private typeContratList: string[] = [];
  private typeFacturationList: string[] = [];
  private clientList: Array<Client> = [];


  constructor(private fb: FormBuilder,
              private route: ActivatedRoute,
              private pageTitleService: PageTitleService,
              private maintenanceService: MaintenanceService,
              private clientService: ClientService,) {
    super();
  }

  ngOnInit() {
    this.loadMaintenance();
    this.pageTitleService.setTitle("MODIFIER UNE MAINTENANCE");
    this.maintenanceForm = this.fb.group({
      id: [Validators.required],
      numero: '',
      date: [null, Validators.compose([Validators.required, CustomValidators.date])],
      client: [null, Validators.required],
      typeContrat: ['', Validators.required],
      typeFacturation: ['', Validators.required],
      montant: [0, Validators.required],
    });
  }

  loadMaintenance() {
    this.route.params.switchMap((params: Params) => this.maintenanceService.getMaintenance(+params['id']))
      .subscribe(maintenance => {
          this.maintenance = maintenance;
          this.maintenanceForm.patchValue(this.maintenance);
          this.loadTypeContratList();
          this.loadTypeFacturationList();
          this.loadClientList();
        }, error => this.showError(error.status, error.json().message)
      );
  }

  updateMaintenance(maintenanceForm: any) {

    this.maintenance = new Maintenance(maintenanceForm);
    this.maintenanceService.saveMaintenance(this.maintenance)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.router.navigate(['/maintenance/list']); // Redierction vers list

      }, error => {

        this.showError(error.status, error.json().message);
      })
    ;
  }

  deleteMaintenance(maintenanceForm: any) {
    if (!confirm("Etes-vous de vouloir supprimer ces enregistrements !")) {
      return;
    }

    this.maintenance = new Maintenance(maintenanceForm);
    this.maintenanceService.deleteMaintenance(this.maintenance)
      .subscribe(() => {
        this.showInfo("Suppression effectué avec succès");
        this.router.navigate(['/maintenance/list']); // Redierction vers list

      }, error => {

        this.showError(error.status, error.json().message);
      });
  }

  private

  loadTypeContratList() {
    this.maintenanceService.getTypeContratList()
      .subscribe(typeContratList => {
          this.typeContratList = typeContratList;
          this.maintenanceForm.patchValue({typeContrat: this.maintenance.typeContrat}, {onlySelf: true});
        }, error => this.showError(error.status, error.json().message)
      )
    ;
  }

  private loadTypeFacturationList() {
    this.maintenanceService.getTypeFacturationList()
      .subscribe(typeFacturationList => {
          this.typeFacturationList = typeFacturationList;
          this.maintenanceForm.patchValue({typeFacturation: this.maintenance.typeFacturation}, {onlySelf: true});
        }, error => this.showError(error.status, error.json().message)
      )
    ;
  }

  private loadClientList() {
    this.clientService.getOnlyClientList()
      .subscribe(clientList => {
          this.clientList = clientList;
          this.maintenance.client = this.clientList.find(client => client.id === this.maintenance.client.id);
          this.maintenanceForm.patchValue({client: this.maintenance.client}, {onlySelf: true});
        }, error => this.showError(error.status, error.json().message)
      )
    ;
  }

}






