import {NgModule} from '@angular/core';
import {RouterModule} from '@angular/router';
import {CommonModule} from '@angular/common';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FileUploadModule} from 'ng2-file-upload/ng2-file-upload';
import {WizardModule} from 'ng2-archwizard';
import {MaintenanceAddComponent}  from './maintenance.add/maintenance.add.component';
import {MaintenanceRoutes} from './maintenance.routing';
import {MaintenanceService} from "./service/maintenance.service";
import {MaintenanceListComponent} from "./maintenance.list/maintenance.list.component";
import {MaintenanceEditComponent} from "./maintenance.edit/maintenance.edit.component";
import {ClientService} from "../client/service/client.service";
import {DataTableModule} from "angular2-datatable";

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    FileUploadModule,
    DataTableModule,
    WizardModule,
    RouterModule.forChild(MaintenanceRoutes)
  ],
  declarations: [
    MaintenanceAddComponent,
    MaintenanceListComponent,
    MaintenanceEditComponent,
  ],
  providers: [
    MaintenanceService,
    ClientService,
  ],
})

export class MaintenanceModule {
}



