import {Routes} from '@angular/router';

import {MaintenanceAddComponent}  from './maintenance.add/maintenance.add.component';
import {MaintenanceListComponent} from "./maintenance.list/maintenance.list.component";
import {MaintenanceEditComponent} from "./maintenance.edit/maintenance.edit.component";

export const MaintenanceRoutes:Routes = [{
  path: '',
  redirectTo: 'list',
  pathMatch: 'full',
}, {
  path: '',
  children: [{
    path: 'add', component: MaintenanceAddComponent
  },
    {path: 'list', component: MaintenanceListComponent},
    {path: 'edit/:id', component: MaintenanceEditComponent},

  ]
}];



