"use strict";

import {Component, ViewChild, OnInit, ViewEncapsulation} from '@angular/core';
import {fadeInAnimation} from "../../core/route-animation/route.animation";
import {Maintenance} from "../metier/maintenance.metier";
import {BaseComponent} from "../../core/component/base-component";
import {MaintenanceService} from "../service/maintenance.service";
import {FormGroup, FormBuilder} from "@angular/forms";
import {Client} from "../../client/metier/client.metier";
import {ClientService} from "../../client/service/client.service";
import {PageTitleService} from "../../core/page-title/page-title.service";

@Component({
  selector: 'ms-maintenance-list',
  templateUrl: './maintenance.list.component.html',
  styleUrls: ['./maintenance.list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]

})

export class MaintenanceListComponent extends BaseComponent implements OnInit {

  public maintenanceForm: FormGroup;
  private maintenance: Maintenance;
  private maintenanceList: Array<Maintenance> = [];
  private typeContratList: string[] = [];
  private typeFacturationList: string[] = [];
  private clientList: Array<Client> = [];

  constructor(private fb: FormBuilder, private pageTitleService: PageTitleService,
              private maintenanceService: MaintenanceService,
              private clientService: ClientService) {
    super();
    this.createForm();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("LISTE DES MAINTENANCES");
    this.loadMaintenanceList();
    this.loadClientList();
    this.loadTypeContratList();
    this.loadTypeFacturationList();
  }

  createForm() {
    this.maintenanceForm = this.fb.group({
      numero: null,
      client: null,
      typeContrat: '',
      typeFacturation: '',
      typePaiement: '',
    });
  }


  private loadMaintenanceList() {

    this.maintenanceService.getMaintenanceList()
      .subscribe(maintenanceList => {
        this.maintenanceList = maintenanceList;
      }, error => {

        this.showError(error.status, error.json().message);
      });
  }

  findMaintenanceList(maintenanceForm: any) {

    this.maintenance = new Maintenance(maintenanceForm);
    this.maintenanceService.findMaintenanceListByCriteria(this.maintenance)
      .subscribe(maintenanceList => {
        this.maintenanceList = maintenanceList;

      }, error => {

        this.showError(error.status, error.json().message);
      });
  }

  private loadTypeContratList() {
    this.maintenanceService.getTypeContratList()
      .subscribe(typeContratList => {
          this.typeContratList = typeContratList;
        }, error => this.showError(error.status, error.json().message)
      );
  }

  private loadTypeFacturationList() {
    this.maintenanceService.getTypeFacturationList()
      .subscribe(typeFacturationList => {
          this.typeFacturationList = typeFacturationList;
        }, error => this.showError(error.status, error.json().message)
      )
    ;
  }

  private loadClientList() {
    this.clientService.getOnlyClientList()
      .subscribe(clientList => {
          this.clientList = clientList;
        }, error => this.showError(error.status, error.json().message)
      );
  }


}



